.icon-editor-container {

  border-radius: 8px;
  width: 100%;
  padding-left: 1%;
}

.icon-editor-title {
  text-align: left;
  color: #373e4a;
  /* Mantido o valor único */
  margin: 20px 0 20px 5px;
  /* Simplificado para margem superior e inferior */
  font-size: 25px;
  font-weight: 500;
  line-height: 1.1;
  font-family: inherit;
}

.icon-editor-form {
  display: flex;
  flex-direction: column;
  grid-gap: 10px;
  gap: 10px;
  /* position: relative; */
  padding-right: 37%;
  align-items: flex-end;
  justify-content: space-between;
}

/* Adiciona um underline suave entre os elementos */
.icon-editor-form>* {
  position: relative;
  /* Necessário para o pseudo-elemento */
}

.icon-editor-form>*:not(:last-child)::after {
  content: "";
  /* Cria um pseudo-elemento vazio */
  position: absolute;
  left: 0;
  /* Alinha à esquerda */
  right: 0;
  /* Alinha à direita */
  bottom: -5px;
  /* Posiciona o underline abaixo do elemento */
  height: 1px;
  /* Espessura do underline */
  background-color: rgba(0, 0, 0, 0.1);
  /* Cor suave para o underline */
  transition: background-color 0.3s;
  /* Suaviza a transição ao passar o mouse */
}



.icon-editor-label {
  font-weight: 600;
  margin-bottom: 8px;
  color: #555;
  width: 313px;

}

.select {
  width: 420px;
  margin-top: 5px;
  margin-bottom: 5px;
  margin-left: 0%;
}

.select-icon {
  width: 313px;
}

.select-icon-modify {
  width: 300px;
  margin-left: 5px;
}

.icon-editor-input {
  margin-left: 8px;
  width: 313px;
  height: 15px;
}

.icon-editor-input-file {
  padding: 5px;
}



